import localStorageObjects from "local-storage-objects";
import configuration from "@/configuration/default.js";

export default class LocalStorage {
    constructor() {
        this.init();
    }

    async init() {
        await this.onLoad();
    }

    async onLoad() {
        this.localStorage = localStorageObjects;
        this.setLanguageKey(configuration.language.key);
    }

    setLanguageKey(language_key = "japanese-vietnamese") {
        this.language_key = language_key;
        this.language = configuration.language.list[this.language_key];
        this.default_level = this.language.data.default;
        this.name = this.language.local_storage.name;
        this.setting = this.getSettingInfo();
    }

    getSettingInfo() {
        let setting = this.localStorage.getItem(this.name);
        if (!setting) {
            setting = configuration.setting;
        }
        return setting;
    }

    getValue(key = null) {
        if (key && key in this.setting) {
            return this.setting[key];
        }

        return null;
    }

    setValue(key = null, value = null) {
        if (key) {
            this.setting[key] = value;
        }

        this.localStorage.setItem(this.name, this.setting);
    }

    getLevel() {
        return this.setting.level;
    }
    setLevel(selected_level = this.default_level) {
        this.setting.level = selected_level;
        this.setValue("level", this.setting.level);
    }
}
