import { createRouter, createWebHistory } from "vue-router";

import routerSmartPhone from "./smartphone/index.js";

import DefaultRouterView from "@/views/layouts/templates/DefaultRouterView.vue";

const routes = [
    {
        path: "",
        // beforeEnter: isAuthenticated,
        component: DefaultRouterView,
        children: [
            {
                path: "",
                component: () => import("@/views/About.vue"),
            },
            {
                path: "/",
                component: () => import("@/views/About.vue"),
            },
            {
                path: "donate",
                component: () => import("@/views/donate/Index.vue"),
            },
            {
                path: "about",
                component: () => import("@/views/About.vue"),
            },
            {
                path: "smartphone",
                component: DefaultRouterView,
                children: routerSmartPhone,
            },
        ],
    },

    {
        path: "/:pathMatch(.*)*",
        name: "error_404",
        component: () => import("@/views/errors/error-404.vue"),
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    // history: createWebHistory(import.meta.env.BASE_URL),
    // history: createWebHashHistory(),
    routes,
});

